  body {
    margin: 0;
    font-family: 'Segoe UI', sans-serif;
  }
  
.home {
  height: 100vh;
  width: 100%;
}

.home-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
  background-color: #ffffff;
  font-family: 'Helvetica Neue', sans-serif;
  color: #111;
}

.home-header {
  margin-bottom: 3rem;
}

.home-header h1 {
  font-size: 3.5rem;
  font-weight: 300;
  margin-bottom: 0.5rem;
}

.divider {
  width: 400;
  height: 1px;
  background-color: #000;
  border: none;
  margin: 1rem 0;
}

.lead-text {
  font-size: 1.25rem;
  font-weight: 500;
  color: #333;
  margin-bottom: 1rem;
}

.subtext {
  font-size: 0.95rem;
  line-height: 1.7;
  color: #666;
  max-width: 700px;
}

.project-preview {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

.project-thumb {
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.project-thumb img {
  width: 100%;
  height: auto;
  object-fit: cover;
  margin-bottom: 0.5rem;
}

.project-thumb p {
  font-size: 0.9rem;
  text-transform: uppercase;
  color: #555;
  letter-spacing: 1px;
}



  .about-container {
  padding: 2rem;
  background-color: #ffffff;
  min-height: 100vh;
  text-align: center;
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}

.about-profile {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #007bff;
  margin-bottom: 1rem;
}

.about-text {
  max-width: 600px;
  text-align: left;
  color: #333;
}

.about-text h3 {
  margin-top: 1rem;
}

.about-text ul {
  list-style: disc;
  padding-left: 1.5rem;
  color: #555;
}

.myphoto{
  width: 20%;
}
.project-page {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Helvetica Neue', sans-serif;
  color: #333;
  background-color: rgb(238, 238, 238);
}

.project-header h1 {
  font-size: 3rem;
  font-weight: 300;
  margin-bottom: 10px;
}

.project-header hr {
  margin: 10px 0;
  border: none;
  border-top: 1px solid #ffffff;
  width: 100%;
}

.project-subtitle {
  font-size: 1.2rem;
  font-weight: 500;
  margin-top: 10px;
}

.project-description {
  font-size: 0.95rem;
  color: #666;
  line-height: 1.6;
  margin-top: 10px;
}

.project-gallery {
  display: flex;
  gap: 20px;
  margin-top: 40px;
}

.project-gallery img {
  width: 100%;
  max-width: 400px;
  height: 200px;
  object-fit: cover;
  border-radius: 4px;
}

.contact-container {
  padding: 2rem;
  background-color: #ffffff;
  min-height: 100vh;
  text-align: center;
  color: #333;
}

.contact-info {
  margin-top: 2rem;
  font-size: 1rem;
}

.contact-info p {
  margin: 0.5rem 0;
}

.contact-info a {
  color: #007bff;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}



